.mapcontainer {
  position: absolute;
  display: block;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  border-radius: 8px;
  border: 0px solid transparent;
  box-shadow: 0 4px 5px 0 rgba(0,0,0,.14), 0 1px 10px 0 rgba(0,0,0,.12), 0 2px 4px -1px rgba(0,0,0,.2);
  .map {
    position: absolute; 
    top: 0; 
    bottom: 0; 
    width: 100%;
  }
  .layer {
    position: absolute;
    display: block;
    left: 10px;
    top: 10px;
    width: 340px;
    height: 280px;
    background: white;
    padding-bottom: 20px;
    padding-top: 0px;
    font-family: 'Open Sans', sans-serif;
    font-size: 14px;
    opacity: 0.75;
    text-align:left;
    /* margin-left: 5px;
    margin-right: 5px; */
    overflow: auto;
  }
  .mapcrosshair {
    position: relative;
    width: 100%;
    height: 100%;
    outline: 0;
    overflow: hidden;
    box-sizing: border-box;
    [class=mapboxgl-canvas] {
      cursor: crosshair;
    }
  }
  .mapidentify {
    z-index: 999;
    position: absolute;
    border-radius: 4px;
    border: 1px solid rgba(0,0,0,.14);
    top: 145px;
    right: 10px;
    width: 30px;
    height: 30px;
  }
  .maplocationtip {
    z-index: 999;
    position:absolute; 
    display: block;
    background-color: white;
    opacity: 0.9;
    margin-left: 10px;
    padding: 0px 5px;
    top: calc(100% - 270px);
    width: 320px;
    height: 20px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    box-shadow: 0 4px 5px 0 rgba(0,0,0,.14), 0 1px 10px 0 rgba(0,0,0,.12), 0 2px 4px -1px rgba(0,0,0,.2);
    border-radius: 4px;
  }
}

.mapboxgl-popup {
  max-width: 400px;
  font: 12px/20px 'Helvetica Neue', Arial, Helvetica, sans-serif;
}