.storycontrollor {
  z-index: 2;
  position: absolute;
  display: block;
  width: 420px;
  /* height: 400px; */
  top: 0px;
  right: 0px;
  bottom: 0px;
  background: white;
  padding-bottom: 20px;
  padding-top: 0px;
  font-family: 'Open Sans', sans-serif;
  font-size: 14px;
  opacity: 0.85;
  text-align:left;
  /* color: white; */
  overflow: scroll;
  .title {
    font-size: 36px;
    font-weight: bold;
    color: #0dbf60;
    position: 'relative';
  }
  .searchBar {
    padding-top: 20px;
  }
}

