.header {
    background-color: #ffffff;
    padding: 0 10px;
    height: 64px;
    line-height: 64px;
    border: 1px solid rgba(0,0,0,.14);
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.5);
    .logo {
      width: 30px;
      height: 30px;
      margin-right: 10px;
      vertical-align: middle;
      float: left;
      margin-top: 10px;
    }
    .title {
      display: inline-block;
      height: 60px;
      margin-left: 10px;
      font-size: 36px;
      line-height: 60px;
      font-weight: bold;
      color: #0ad3a8;
    }
    .avatar {
      width: 48;
      height: 68;
      margin-right: 10px;
      img,
      .avatarTxt {
        display: inline-block;
        vertical-align: middle;
      }
    }
  
    .userinfo {
      float: right;
      margin-right: 10px;
    }
    .name {
      height: 40px;
      display: inline-block;
      padding: 0 5px;
    }
}

.content {
    min-height: 400px;
    .page1 {
        .main {
            background-image: url('../../data/rebookhotel/main_image.png');
            background-repeat: no-repeat;
            background-size: 100%;
            height: 640px;
            min-height: 640px;
        }
        .info {
            text-align: center;
            font-weight: bold;
            background-color: #ffffff;
            color: #0ad3a8;
            font-size: 36px;
            line-height: 64px;
        }
    }
    .page2 {
        background-color: #ffffff;
        height: 540px;
        padding-top: 20px;
    }
    .page3 {
        background-color: #ffffff;
        background-color: #ffffff;
        height: 480px;
        padding-top: 20px;
        text-align: center;
        font-size: 24px;
        .info {
            text-align: center;
            font-weight: bold;
            background-color: #ffffff;
            color: #0ad3a8;
            font-size: 36px;
            font-family: "黑体", Arial, Helvetica, sans-serif;
            line-height: 64px;
        }
    }
}

.footer {
    color: hsl(231, 43%, 45%);
    background-color: #ffffff;
    padding: 0 10px;
    height: 64px;
    line-height: 64px;
}


.createaccount {
    margin-left: auto;
    margin-right: auto;
    width: 50%;
    background-color: #0ad3a8;
}

.hoteltrack {
    margin-left: auto;
    margin-right: auto;
    width: 50%;
    background-color: white;
}